<template>
  <scroll-view class="scroll-view"
    :data="albumList"
    pullUpLoad
    @pullUp="onload"
  >
    <div class="album">
      <div class="list-item" v-for="(e, i) in albumList" :key="i">
        <p>{{e.album_name}}</p>
        <p class="singer">{{e.singer_name}}</p>
      </div>
    </div>
  </scroll-view>
</template>
<script>
import { mapState } from 'vuex'
import ScrollView from '#/ScrollView'

export default {
  name: 'Rank',
  components: {
    ScrollView,
  },
  data() {
    return {
    }
  },
  computed: {
    ...mapState('music', ['albumList'])
  },
  methods: {
    onload(e) {
      this.$store.dispatch('music/albumList')
    },
  },
  created() {
    this.onload()
  }
}
</script>
<style lang="scss" scoped>
.scroll-view {
  height: calc(100vh - 44px);
}
.list {
  background-color: #fff;
  &-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #eee;
    p {
      font-size: .32rem;
      padding: .2rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .singer {
      flex: 0 0 2rem;
      font-size: .28rem;
      color: #999;
    }
  }
}
</style>


