<template>
  <div class="search">
    <van-search
      v-model="value"
      placeholder="搜索歌曲、歌单、专辑"
      show-action
      @search="onSearch"
      @cancel="onCancel"
    />

    <!-- history -->
    <div v-if="historySearch.length && !showSongs" class="history">
      <div v-for="(e, i) in historySearch" :key="i" class="item">
        <van-icon name="clock" />
        <p @click="research(e)">{{e}}</p>
        <van-icon name="cross" @click="deleteHistorySearch"/>
      </div>
      <div class="clear" @click="clearHistorySearch">清空历史记录！</div>
    </div>

    <!-- song list -->
    <div v-if="showSongs" class="list">
      <div class="list-item" v-for="(e, i) in songs" :key="i" @click="play(i)">
        <van-icon name="music-o" />
        <div class="list-info">
          <h3>{{e.songname}}</h3>
          <p>{{e.singer[0].name}}</p>
        </div>
      </div>
    </div>

    <!-- hotkey -->
    <div v-if="hotKey && showHot && !history.size" class="hot">
      <h3>热门搜索</h3>
      <p class="special" @click="research(hotKey.special_key)">{{hotKey.special_key}}</p>
      <p v-for="(e, i) in hotKey.hotkey" :key="i" @click="research(e.k)">{{e.k}}</p>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import { search} from '@/api'
export default {
  name: 'Search',
  data() {
    return {
      value: '',
      showHot: true,
      showSongs: false,
      history: [],
    }
  },
  computed: {
    ...mapState('music', [
      'hotKey',
      'historySearch'
    ]),
    ...mapState({
      songs: state => state.player.songList,
    }),
  },
  created() {
    this.$store.dispatch('music/hotkey')
  },
  methods: {
    ...mapMutations('music', [
      'addHistorySearch',
      'clearHistorySearch',
      'deleteHistorySearch',
    ]),
    ...mapMutations('player', [
      'songList',
    ]),
    research(value) {
      this.value = value
      this.onSearch()
    },
    onSearch() {
      this.showHot = false
      this.showSongs = true
      this.addHistorySearch(this.value)
      search(this.value, 1).then(res => {
        if (!res.code) {
          this.songList(res.data.song.list)
        }
      })
    },
    onCancel() {
      this.showHot = true
      this.showSongs = false
    },
    play(i) {
      this.$store.dispatch('player/play', i)
    }
  }
}
</script>
<style lang="scss" scoped>
.search {
  background-color: #fff; }
.hot {
  overflow: hidden;
  padding: .3rem;
  h3 {
    font-size: 14px;
    font-family: "hiragino sans gb";
    font-weight: bolder;
    margin-bottom: .15rem; }
  p {
    float: left;
    margin: 0 .2rem .2rem 0;
    padding: 0 .15rem;
    font-size: 15px;
    line-height: .6rem;
    border: 1px solid #6c6c6c;
    height: .6rem;
    border-radius: 30px; }
  .special {
    color: #fc4524; } }
.list {
  position: relative;
  border: 1px solid #eee;
  &-item {
    display: flex;
    align-items: center;
    height: 1.1rem;
    font-size: 15px;
    padding-right: .3rem;
    border-bottom: 1px solid #eee;
    .van-icon-music-o {
      flex: 0 0 1rem;
      text-align: center;
      font-size: 26px;
    } }
  &-info {
    position: relative;
    width: 85%;
    h3 {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding: .15rem 0; }
    p {
      font-size: 12px;
      color: #808080; } } }
.history {
  position: relative;
  .item {
    position: relative;
    p {
      height: 0.88rem;
      text-indent: 1rem;
      line-height: 0.88rem;
      font-size: 16px;
      border-bottom: 1px solid #f6f6f6; }
    .van-icon {
      position: absolute;
      top: 50%;
      font-size: 18px;
      transform: translateY(-50%); }
    .van-icon-clock {
      left: .2rem;
      color: #cbcbcb; }
    .van-icon-cross {
      right: .2rem;
      color: #959595; } }
  .clear {
    font-size: 14px;
    text-align: center;
    padding: .3rem;
    color: #72d5a5; } }
</style>


