<template>
  <div class="main">
    <van-tabs sticky animated v-model="active" title-active-color="#57cd94" :line-height="2" color="#57cd94" title-inactive-color="#666">
      <van-tab title="推荐">
        <recommend />
      </van-tab>
      <van-tab title="专辑">
        <album />
      </van-tab>
      <van-tab title="排行榜">
        <rank />
      </van-tab>
      <van-tab title="搜索">
        <search />
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import Recommend from './Recommend'
import Rank from './Rank'
import Search from './Search'
import Album from './Album'
export default {
  components: {
    Recommend,
    Rank,
    Search,
    Album,
  },
  data() {
    return {
      active: 0,
    }
  },
}
</script>

<style lang="scss" scoped>
.main {
  background-color: #f4f4f4;  
}
</style>



