<template>
  <scroll-view class="scroll-view">
  <div class="recommend">
    <van-swipe :autoplay="3000" indicator-color="white" :height="150">
      <van-swipe-item v-for="(e, i) in slider" :key="i" @click="$dialog.alert({message:'接口开发中···'})">
        <van-image width="100%" :src="e.picUrl" />
      </van-swipe-item>
    </van-swipe>
      <van-tabs v-model="active" swipeable animated>
        <van-tab v-for="(e, i) in mvData" :key="i" :title="i">
          <van-grid :column-num="2" :gutter="10">
            <van-grid-item @click="toast" v-for="(el, idx) in e" :key="idx">
              <div class="grid-item">
                <van-image width="100%" :src="el.picurl" fit="contain" />
                <p class="title">{{el.mvtitle}}</p>
                <p class="desc">{{el.mvdesc}}</p>
              </div>
            </van-grid-item>
          </van-grid>
        </van-tab>
      </van-tabs>
  </div>
  </scroll-view>
</template>
<script>
import { mapState } from 'vuex'
import ScrollView from '#/ScrollView'
import { Toast } from 'vant'
export default {
  name: 'Recommend',
  components: {
    ScrollView,
  },
  data() {
    return {
      active: 0,
    }
  },
  methods: {
    toast() {
      Toast('接口开发中，可以试试搜索哦')
    }
  },
  created() {
    this.$store.dispatch('music/homeData')
  },
  computed: {
    ...mapState('music', ['slider', 'mvData'])
  }
}
</script>
<style lang="scss" scoped>
/deep/ .van-grid-item__content {
  padding: 0; }
.scroll-view {
  height: calc(100vh - 44px);
}
.wrap {
  height:100vh; }
.recommend {
  background-color: #fff; }
.grid-item {
  position: relative;
  width: 100%;
  height: 100%;
  .title {
    padding: .1rem;
    font-size: 14px;
    line-height: 1.2; }
  .desc {
    position: absolute;
    top: 0;
    left: 0;
    padding: .1rem;
    font-size: 14px;
    color: #f6f6f6; } }
</style>


